<template>
  <div>
    <button @click="toggleModuleInfo" class="takenModule">{{takenModule.title}}</button>
    <div class="content" v-bind:class="{closed: isClosed , open: !isClosed}">
      <p>{{takenModule.desc}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "TakenModuleItem",
  props: ["takenModule"],
  data() {
    return { isClosed: true };
  },
  methods: {
    toggleModuleInfo() {
      this.isClosed = !this.isClosed;
    }
  }
};
</script>

<style scoped>
.takenModule {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active,

.takenModule{
  text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.takenModule:hover {
  background-color: #ccc;
}
.closed {
  display: none;
}
.open {
  display: block;
  background: #444;
}

</style>